import styled from "@emotion/styled";
import {
  brightPurple,
  darkYellow,
  purple,
  white,
  yellow,
} from "../styles/colors";
import { Container, HoverAnimation } from "../styles/patterns";

function Header({ onMenuClick }: { onMenuClick: () => void }) {
  return (
    <HeaderContainer>
      <Logo>
        <LogoInside></LogoInside>
      </Logo>
      <Center>
        <Title>–ú–∏–Ω–∏–º–∞–ª–∏—Å—Ç–∏—á–Ω—ã–π</Title>
        <Title>–∫–∞–ª—å–∫—É–ª—è—Ç–æ—Ä</Title>
        <Title> –∫–∞–ª–æ—Ä–∏–π</Title>
      </Center>
      <MenuButton
        type="button"
        onClick={onMenuClick}
        aria-label="–û—Ç–∫—Ä—ã—Ç—å –º–µ–Ω—é"
      ></MenuButton>
    </HeaderContainer>
  );
}

export default Header;

const HeaderContainer = styled.header`
  ${Container};
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  border-bottom: 1px solid ${yellow};
`;

const Title = styled.h1`
  font-size: 20px;
  line-height: 22px;
  font-weight: 400;
  color: ${yellow};
  text-shadow: 0 1px 1px ${purple};
  margin: 0;
`;

const Center = styled.div``;

const Logo = styled.div`
  height: 19px;
  width: 22px;
  background: ${yellow};
  border: 2px solid ${purple};
  border-top-left-radius: 6px;
  border-top-right-radius: 10px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 6px;
`;

const LogoInside = styled.div`
  height: 19px;
  width: 19px;
  box-sizing: border-box;
  background: radial-gradient(circle, ${brightPurple} 0%, ${yellow} 50%);
  border: 2px solid ${brightPurple};
  border-radius: 99em;
  margin: auto;
`;

const MenuButton = styled.button`
  height: 19px;
  width: 22px;
  background: linear-gradient(
    180deg,
    ${darkYellow} 0,
    ${darkYellow} 3px,
    ${white} 3px,
    ${white} 8px,
    ${purple} 8px,
    ${purple} 11px,
    ${white} 11px,
    ${white} 16px,
    ${darkYellow} 16px,
    ${darkYellow} 19px
  );
  border: none;
  outline: none;
  margin: 0;

  :hover {
    margin: 2px 0 0 0;
  }

  ${HoverAnimation}
`;
