import * as Yup from "yup";

const schema = Yup.number()
  .nullable()
  .max(100, "?")
  .positive("?")
  .required("?");

export const validationSchema = Yup.object({
  protein: schema,
  fat: schema,
  carb: schema,
  weight: Yup.number().max(1000, "?").positive("?").required("?"),
});
