import styled from "@emotion/styled/macro";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { loadState } from "../redux/mealSlise";
import { State } from "../redux/types";
import { purple } from "../styles/colors";
import { Container } from "../styles/patterns";
import Footer from "./Footer";
import Header from "./Header";
import Main from "./Main";
import MenuPopup from "./MenuPopup";

function App() {
  const state = useSelector((state: State) => state);
  const dispatch = useDispatch();

  const [menuPopupOpen, setMenuPopupOpen] = useState(false);

  function closeAllPopups() {
    setMenuPopupOpen(false);
  }

  function handleMenuClick() {
    setMenuPopupOpen(true);
  }

  function onKeydown({ key }: KeyboardEvent) {
    if (key === "Escape") {
      closeAllPopups();
    }
  }

  const localStorageKey = "mealState";

  // –ó–∞–≥—Ä—É–∑–∫–∞ –¥–∞–Ω–Ω—ã—Ö –∏–∑ –ª–æ–∫–∞–ª—å–Ω–æ–≥–æ —Ö—Ä–∞–Ω–∏–ª–∏—â–∞ –≤ —Å—Ç–µ–π—Ç
  useEffect(() => {
    const savedState = localStorage.getItem(localStorageKey);

    if (savedState) {
      try {
        dispatch(loadState(JSON.parse(savedState).meals));
      } catch {}
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // –ó–∞–≥—Ä—É–∑–∫–∞ –¥–∞–Ω–Ω—ã—Ö –∏–∑ —Å—Ç–µ–π—Ç–∞ –≤ –ª–æ–∫–∞–ª—å–Ω–æ–µ —Ö—Ä–∞–Ω–∏–ª–∏—â–µ
  useEffect(() => {
    localStorage.setItem(localStorageKey, JSON.stringify(state));
  }, [state]);

  return (
    <AppContainer>
      <MenuPopup
        isOpen={menuPopupOpen}
        onClose={closeAllPopups}
        onKeydown={onKeydown}
      />
      <Header onMenuClick={handleMenuClick} />
      <Main />
      <Footer />
    </AppContainer>
  );
}

export default App;

const AppContainer = styled.div`
  ${Container};

  max-width: 500px;
  min-width: 320px;
  position: relative;
  text-align: center;
  color: ${purple};
  margin: 0 auto;
  padding: 0 10px 20px;
`;
